import React from 'react'
import './Subscription.css'
import his from './history.png'

export default function History() {

    return (
        <div onLoad={()=>{
            document.title="YouTube"
        }}>
            <div className="body">
                <div className="icon">
                    <img src={his} alt="" />
                    <div className="notes">
                        <h>
                        Keep track of what you watch
                        </h>
                        <p>
                        Watch history isn't viewable when you're signed out. <a href="/">Learn more</a> 
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}
