import React from 'react'
import './Suggestion.css';
export default function Suggestion(props) {
    return (
        <div>
            <div className="suggest">
                <div className="s">
                    <a href="/"><p>{props.s0}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s1}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s2}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s3}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s4}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s5}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s6}</p></a>
                </div>
                <div className="s">
                    <a href="/"><p>{props.s7}</p></a>
                </div>
            </div>
        </div>
    )
}
