import React from 'react'
import './Leftbar.css';
import home from './home.png'
import explore from './explore.png'
import sbs from './subscription.png'
import library from './library.png'
import history from './history.png'
import acc from './account.png'


import {
    Link
} from "react-router-dom";

export default function Leftbar() {
    return (
        <div className="bar">
            <div className="item1">

                <Link to="/">
                    <div className="items">
                        <img src={home} alt="" />
                        <p>Home</p>
                    </div>
                </Link>

                <Link to="/Explore">
                    <div className="items">
                        <img src={explore} alt="" />
                        <p>Explore</p>
                    </div>
                </Link>

                <Link to="/Subscription">
                    <div className="items">
                        <img src={sbs} alt="" />
                        <p>Subscriptions</p>
                    </div>
                </Link>
            </div>
            <div className="item2">

                <Link to="/Library">
                    <div className="items">
                        <img src={library} alt="" />
                        <p>Library</p>
                    </div>
                </Link>

                <Link to="/History">
                    <div className="items">
                        <img src={history} alt="" />
                        <p>History</p>
                    </div>
                </Link>
            </div>

            <div className="item3">
                <p>Sign in to like videos, comment and subscribe.</p>
                <div className="signin">
                    <img src={acc} alt="" />
                    <h5>SIGN IN</h5>
                </div>
            </div>

        </div>
    )
}
