import React from 'react'
import './Subscription.css'
import sbs from './subscription.png'
export default function Subscription() {
    return (
        <div className="body" onLoad={()=>{
            document.title="Subscriptions - YouTube"
        }}>
            <div className="icon">
                <img src={sbs} alt="" />
                <div className="notes">
                    <h>
                        Don't miss new videos
                    </h>
                    <p>
                        Sign in to see updates from your favourite YouTube channels
                    </p>
                </div>
            </div>
        </div>
    )
}
